@import url(https://fonts.googleapis.com/css2?family=Hammersmith+One&display=swap);
body {
	font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
		Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	margin: 20px;
}

ol,
ul {
	padding-left: 30px;
}

.board-row:after {
	clear: both;
	content: "";
	display: table;
}

.status {
	margin-bottom: 10px;
	font-size: 80px;
}

.square {
	background: #fff;
	border: 1px solid #999;
	float: left;
	font-size: 50px;
	font-weight: bold;
	line-height: 100px;
	height: 100px;
	margin-right: 1px;
	margin-top: 1px;
	padding: 10;
	text-align: center;
	width: 100px;
}

.square:focus {
	outline: none;
}

.kbd-navigation .square:focus {
	background: #ddd;
}

.game {
	display: flex;
	flex-direction: row;
}

.game-info {
	font-size: 20px;
	font-weight: bold;
	margin: 50px;
}

.move-btn {
	font-size: 20px;
	padding: 5px;
	margin-bottom: 8px;
}

